.page {
  margin: 20px;
  text-align: center;
}

@media (max-width: 600px) {
  .page {
    margin: 20px 0px;
  }
}

.table {
  margin: 20px 0px;
  width: 100%;
}

@media (max-width: 600px) {
  .table {
    overflow: scroll;
  }
}

.table-header {
  display: flex;
  line-height: 24px;
  font-size: inherit;
  padding: 0 5px;
  justify-content: space-between;
}

@media (max-width: 600px) {
  .table-header {
    width: max-content;
  }
}

.table-empty {
  margin: 200px;
  text-align: center;
  font-size: 16px;
}

.table-row {
  position: relative;
  display: flex;
  line-height: 24px;
  margin: 10px 0;
  padding: 10px 5px;
  background: #ffffff;
  border: 1px solid #e3e3e3;
  width: 100%;
}

@media (max-width: 600px) {
  .table-row {
    width: max-content;
  }
}

.table-header > span {
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 5px;
}

.table-row > span {
  position: relative;
  box-sizing: border-box;
  overflow-wrap: anywhere;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 5px;
}

.table-header > span > *, .table-row > span > * {
  width: 100%;
  text-align: left;
}

.table .span-title {
  width: 40%;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-align: left;
}

.table .span-author {
  width: 30%;
}

.table .span-comments {
  width: 10%;
}

.table .span-points {
  width: 10%;
}

.table .span-archive {
  width: 10%;
}

span a {
  text-decoration: underline;
}

@media (max-width: 600px) {
  .table .span-title {
    width: 200px;
  }

  .table .span-author {
    width: 130px;
  }

  .table .span-comments {
    width: 90px;
  }

  .table .span-points {
    width: 90px;
  }

  .table .span-archive {
    width: 90px;
  }
}

.button-inline {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  width: auto;
  width: 100px;
  border-width: 0;
  background: transparent;
  color: inherit;
  text-align: inherit;
  -webkit-font-smoothing: inherit;
  padding: 0;
  font-size: inherit;
  cursor: pointer;
  overflow: hidden;
}

.button-inline > .imageHolder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: auto;
  margin-right: 2px;
}

.imageHolder > img {
  height: 15px;
  width: 15px;
}

.button-active {
  border-radius: 0;
  border-bottom: 1px solid #000000;
}
